import { ColumnDef, Row } from '@tanstack/react-table';
import adel from '@/assets/imgs/user.png';
import { Employee } from '@/types/employee.types';
import EditIcon from '@/components/icons/EditIcon';
import { Link } from 'react-router-dom';
import { ShiftType } from '@/types/shift.types';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<ShiftType> }) => {
    return (
        <div className="cursor-pointer flex justify-center">
            <Link to={`/human-resources/shifts/${1}/edit`}>
                <EditIcon className="text-primery-blue-400" />
            </Link>
        </div>
    );
};

export const columns: ColumnDef<Employee>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-nowrap text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الاسم
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="flex flex-row justify-start items-center gap-4 text-nowrap text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                <img src={adel} alt="" className="rounded-full w-10 h-10 object-cover" />
                <div className="flex flex-col gap-1">
                    <p className="tracking-normal text-primery-blue-900 font-medium text-sm">{row.original?.name}</p>
                    <p className="tracking-normal text-white-blue-800 font-normal text-sm">{row.original?.email}</p>
                </div>
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-nowrap text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    ايام العطلات
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-nowrap text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                {row.original?.employee_number}
            </div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-nowrap text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    نوع الوردية
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original?.main_salary}
                </div>
            );
        },
    },
    {
        accessorKey: 'date',
        header: () => {
            return (
                <div className="text-nowrap text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    بداية الوردية
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {/* {row.original?.sms_count + row.original?.email_count} */}
                    22/10/2023
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-nowrap text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    نهاية الوردية
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original?.contract_type}
                </div>
            );
        },
    },
    {
        accessorKey: 'location1',
        header: () => {
            return (
                <div className="text-nowrap text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    بداية تسجيل الدخول
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original?.contract_type}
                </div>
            );
        },
    },
    {
        accessorKey: 'location2',
        header: () => {
            return (
                <div className="text-nowrap text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    نهاية تسجيل الدخول
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original?.contract_type}
                </div>
            );
        },
    },
    {
        id: 'actions',
        header: () => {
            return (
                <div className="text-sm text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    التقرير
                </div>
            );
        },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
